import React from "react";
import ReactDOM from "react-dom";

function App(){
  return (
  <React.Fragment>
    <Icon imageSrc="./images/Icon-Note.png" altxt="Notepad icon" text="About me"/>
    <Icon imageSrc="./images/Icon-World.png" altxt="Planet earth icon" text="My work so far"/>
    <Icon imageSrc="./images/Icon-Phone.png" altxt="Phone icon" text="Contact me"/>
  </React.Fragment>
  );
}

const Icon = (props) => {
  return (
    <div className="icon draggable">
      <img src={props.imageSrc} alt={props.altxt}/>
      <p>{props.text}</p>
    </div>
  );
};

ReactDOM.render(<App/>, document.getElementById("reactapp"));